import { useNavigate } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";
import { context } from "../App";
import { useContext } from "react";
import "./css/Header.css";

export default function Header({ user }) {
  const { set_auth } = useContext(context);
  const navigate = useNavigate();

  const logout = () => {
    axios.post("/logout").then((response) => {
      toast.success("   ");
      set_auth(false);
      navigate("/auth");
    });
  };

  return (
    <>
      <div className="links-container">
        <a className="link" href="tel:+74950330363" data-sign="whatsapp">
           
        </a>
        <a className="link" href="tel:+84955000363" data-sign="call">
          84955000363
        </a>
        <a className="link" href="tel:+88005500363" data-sign="call">
          88005500363
        </a>
        . 
      </div>
      <div className="Header">
        <div className="Logo"></div>
        <div className="Title">
            
          <br />
          <span className="Subtitle">   </span>
        </div>
        <div className="lc">
          {user.name} {user.lastname}
          <p className="LinkButton small-btn" onClick={logout}>
            
          </p>
        </div>
      </div>
      <div className="Divider"></div>
      <div className="Button-Container">
        <LinkButton url="/groups"></LinkButton>
        <LinkButton url="/students"></LinkButton>
        <LinkButton url="/staff"></LinkButton>
        <LinkButton url="/event-card-list"> </LinkButton>
        <LinkButton url="/events-log">  </LinkButton>
        <LinkButton url="/report"></LinkButton>
      </div>
    </>
  );
}

const LinkButton = ({ children, url = null }) => {
  const navigate = useNavigate();
  return (
    <div
      className="LinkButton"
      onClick={() => {
        navigate(url);
      }}
    >
      {children}
    </div>
  );
};
